'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Sheet, SheetContent, SheetTrigger } from '@/components/ui/sheet';
import { Menu } from 'lucide-react';

const navLinks = [
  { href: '#about', label: 'About' },
  { href: '#projects', label: 'Projects' },
  { href: '#skills', label: 'Skills' },
  { href: '#contact', label: 'Contact' },
];

export default function Header() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 10);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const NavContent = ({ isMobile = false }) => (
    <nav
      className={`flex items-center gap-2 ${isMobile ? 'flex-col space-y-4 pt-8' : 'gap-6'
        }`}
    >
      {navLinks.map(({ href, label }) => (
        <a
          key={href}
          href={href}
          onClick={() => isMobile && setOpen(false)}
          className="text-base font-medium text-muted-foreground transition-colors hover:text-foreground hover:text-primary"
        >
          {label}
        </a>
      ))}
    </nav>
  );

  return (
    <header
      className={`sticky top-0 z-50 w-full border-b transition-all ${isScrolled ? 'border-border bg-background/80 backdrop-blur-sm' : 'border-transparent'
        }`}
    >
      <div className="container mx-auto flex h-16 max-w-7xl items-center justify-between px-4">
        <a href="#" className="text-xl font-bold font-headline transition-colors hover:text-primary">
          Abhinav Nalla
        </a>

        <div className="hidden md:flex">
          <NavContent />
        </div>

        <div className="md:hidden">
          <Sheet open={open} onOpenChange={setOpen}>
            <SheetTrigger asChild>
              <Button variant="ghost" size="icon">
                <Menu />
              </Button>
            </SheetTrigger>
            <SheetContent side="right">
              <div className="flex flex-col items-center">
                <NavContent isMobile={true} />
              </div>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </header>
  );
}
